@font-face {
    font-family: "YS Text";
    font-style: normal;
    font-weight: 900;
    src: url("https://yastatic.net/s3/home/fonts/ys/4/text-black.woff") format("woff");
}

@font-face {
    font-family: "YS Text";
    font-style: normal;
    font-weight: 500;
    src: url("https://yastatic.net/s3/home/fonts/ys/4/text-medium.woff") format("woff");
}

@font-face {
    font-family: "YS Text";
    font-style: normal;
    font-weight: 400;
    src: url("https://yastatic.net/s3/home/fonts/ys/4/text-regular.woff") format("woff");
}

#root {
    max-width: 1900px;
    min-width: 300px;
    margin-right: auto;
    margin-left: auto;
    overflow: hidden;
}

h1, h2, h3 {
    margin-bottom: 48px;
    color: #21201F;
    font-family: YS Text;
    font-style: normal;
    font-weight: 900;
    line-height: 100%;
}

p {
    color: #21201F;
    font-family: YS Text;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 120%;
}

a {
    color: #C5C5C5;
    text-decoration: underline;
}

.container {
    max-width: 928px;
    margin: 0 auto;
    padding: 0 32px;
}

@media (max-width: 376px) {
    h1 {
        margin-bottom: 35px;
        font-size: 36px;
        letter-spacing: -0.72px;
    }

    h2, h3 {
        margin-bottom: 32px;
        font-size: 32px;
        letter-spacing: -0.64px;
    }

    p {
        font-size: 16px;
    }
}