und__gradient::after{content:"";position:absolute;top:0;right:0;bottom:0;left:0;background:linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFF 100%)}@media(max-width: 577px){.welcome__wrapper{flex-direction:column;gap:30px}.welcome__title{font-size:64px}.welcome__button{margin-bottom:50px}.welcome__images{width:100%;height:300px}.welcome__blur{display:none}.welcome__img{width:320px;height:100%;top:-15%;left:calc(50% - 180px)}.welcome__img::after{width:85px;height:100%;top:12.5%;left:25.5%}}@media(max-width: 376px){.welcome__content{padding-top:32px}.welcome__logo{width:112px;margin-bottom:32px}.welcome__title{margin-bottom:35px;font-size:36px;letter-spacing:-0.72px}.welcome__text{margin-bottom:0;font-size:18px}.welcome__price{padding-left:7px}.welcome__price::before{width:75px;height:23px;top:-1px;left:27px;padding-top:0px;padding-right:5px;font-size:13px}.welcome__price::after{width:56px;height:23px;top:-1px}.welcome__button{position:fixed;top:calc(100vh - 82px);left:calc(50% - 155px);width:calc(100% - 64px);z-index:99}}